.progress {
    height: 10px;
    box-shadow: none;
    background: #e6e6e6;
    overflow: visible;
}
.progress-bar {
    background: #3e64ff;
    box-shadow: none;
    font-size: 12px;
    line-height: 1.2;
    color: #000000;
    font-weight: 600;
    position: relative;
    overflow: visible;
    border-radius: 2px;
}
.progress-bar:after {
    position: absolute;
    top: -2px;
    right: 0;
    width: 34px;
    height: 34px;
    content: '';
    background: #3e64ff;
    border-radius: 2px;
    opacity: 0;
}
.progress-bar div {
    position: absolute;
    top: -38px;
    right: 0;
    font-size: 16px;
    font-weight: 500;
    color: #000000;
}
.progress-wrap {
    width: 100%;
    margin-bottom: 30px;
}
.progress-wrap h3 {
    font-size: 16px;
    margin-bottom: 10px;
    font-weight: 500;
}
